import React, { useState, useEffect } from "react";
import DashboardForm from "./DashboardForm";
import { getTweets } from "../../api/tweets";

const Dashboard = () => {
  const [tweets, setTweets] = useState([]);

  useEffect(() => {
    let unmounted = false;
    getTweets()
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        if (!unmounted) {
          console.log(res.resp);
          let greaterTen = [];

          for (let i = 0; i < res.userTweets?.length; i++) {
            var currentNumber = res.userTweets[i];
            if (currentNumber.hasOwnProperty("in_reply_to_user_id")) {
            } else {
              greaterTen.push(currentNumber);
            }
          }
          if (greaterTen) {
            setTweets(greaterTen);
          }
        }
      })
      .catch((err) => {
        console.error(err.status);
      });

    return () => {
      unmounted = true;
    };
  }, []);

  return (
    <>
      <DashboardForm tweets={tweets} />
    </>
  );
};

export default Dashboard;
